  // width: 100vw;
  // height: 37.5rem;
  // left: 50%;
  // transform: translateX(-50%);
  // // transform: translateX(-200%);
  // padding: 3.5rem 1rem 3.7rem 1rem;
  // background: #222222;
  // will-change: transform;
  // transition: transform 0.5s ease-in-out;

  // &--active {
  //   left: 50%;
