  // }

  // .socials {
  //   display: block;

  //   @media (min-width: 1024px) {
  //     display: none;
  //   }
  // }

  @media (min-width: 1024px) {
    margin-right: 2.5rem;
    // position: static;
    // height: auto;
    // width: auto;
    // transform: translateX(0);
    // background: transparent;
  }

  @media (min-width: 1200px) {
    margin-right: 4rem;
  }

  @media (min-width: 1440px) {
    margin-right: 5rem;
  }

  ul {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: center;

    @media (min-width: 1024px) {
      flex-direction: row;
      justify-content: space-between;
    }

    li {
      // height: 1.5rem;
      margin-bottom: 4rem;

      @media (min-width: 1024px) {
        margin-bottom: 0;
      }
      
      &:not(:last-of-type) {

        @media (min-width: 1024px) {
          margin-right: 2.64rem;
        }
      }

      a {
        padding: 0.5rem;
        @include AppleSDGothicNeoMedium();
        font-size: 1.8rem;
        line-height: 2.2rem;
        text-transform: uppercase;
        color: var(--white-main);
      }
    }
  }
}



